import React, { useState } from "react";
import "../Css/home.css";
import Draggable from "react-draggable";


const Grid = (props) => {

    const [fontSize, SetFontSize] = useState(13);

    return (

        <>
            <Draggable >
                <div className="card " >
                    <div className="card-body" onClick={() => SetFontSize(fontSize + 2)} >
                        <p style={{fontSize:`${fontSize}px`}}>{props.thumbnail}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.image}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.Clipname}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.Slogname}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.Channelname}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.Status}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.Loop}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.Start_Time}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.Duration}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.Down_Counter}</p>
                        <p style={{fontSize:`${fontSize}px`}}>{props.up_Counter}</p>
                    </div>
                </div>
            </Draggable>

        </>
    );
}
export default Grid;