import React, { useEffect, useState, useRef } from "react";

import Filefetch from "../jsonFile/Filefetch";
import Grid from "./Grid.jsx"


const Home = () => {


    const elemRef = useRef(null)
    const dragProps = useRef()

    const initialiseDrag = event => {
        const { target, clientX, clientY } = event
        const { offsetTop, offsetLeft } = target
        const { left, top } = elemRef.current.getBoundingClientRect()


        dragProps.current = {
            dragStartLeft: left - offsetLeft,
            dragStartTop: top - offsetTop,
            dragStartX: clientX,
            dragStartY: clientY,
        }
        window.addEventListener('mousemove', startDragging, false)
        window.addEventListener('mouseup', stopDragging, false)
    }


    const startDragging = ({ clientX, clientY }) => {
        elemRef.current.style.transform = `translate(${dragProps.current.dragStartLeft + clientX - dragProps.current.dragStartX}px, ${dragProps.current.dragStartTop + clientY - dragProps.current.dragStartY}px)`
    }

    const stopDragging = () => {
        window.removeEventListener('mousemove', startDragging, false)
        window.removeEventListener('mouseup', stopDragging, false)
    }

    // Api call

    const [users, setUsers] = useState([]);
    const getUser = async () => {
        const response = await fetch('https://api.github.com/users');
        // console.log(response);
        setUsers(await response.json());
        // console.log(data);
    }

    useEffect(() => {
        getUser();
    }, []);
    return (
        <>


            <div className="container-fluid" >
                <div className="row" >
                    <div className="col">
                        <div className="row">
                            <div className="col">
                                <div className="row dragg " >
                                    {
                                        Filefetch.map((val, index) => {
                                            return <Grid key={index}
                                                thumbnail={val.thumbnail}
                                                Clipname={val.Clipname}
                                                Slogname={val.Slogname}
                                                Channelname={val.Channelname}
                                                Status={val.Status}
                                                Loop={val.Loop}
                                                Start_Time={val.Start_Time}
                                                Duration={val.Duration}
                                                Down_Counter={val.Down_Counter}
                                                up_Counter={val.up_Counter}
                                            />
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container-fluid handle">
                <div className="row">
                    <div className="col">
                        <div className="row">
                            <div className="col">
                                <div className="row text-center">
                                    {
                                        users.map((curelet, index) => {
                                            return (
                                                <div className="row Grid_Table mt-1" key={index}>
                                                    <div className="col Grid_Table1 bg-danger" onMouseDown={initialiseDrag}
                                                        ref={elemRef}>
                                                        <img src={curelet.avatar_url} alt={curelet.avatar_url} height="100px" width="90px" />
                                                    </div>
                                                    <div className="col Grid_Table1" onMouseDown={initialiseDrag}
                                                        ref={elemRef} >
                                                        <h6>{curelet.login}</h6>
                                                    </div>
                                                    <div className="col Grid_Table1">
                                                        <h6>{curelet.type}</h6>
                                                    </div>
                                                    <div className="col Grid_Table1">
                                                        <h6>{curelet.login}</h6>
                                                    </div>
                                                    <div className="col Grid_Table1">
                                                        <h6>{curelet.login}</h6>
                                                    </div>
                                                    <div className="col Grid_Table1" >
                                                        <h6>{curelet.login}</h6>
                                                    </div>
                                                    <div className="col Grid_Table1">
                                                        <h6>{curelet.login}</h6>
                                                    </div>
                                                    <div className="col Grid_Table1">
                                                        <h6>{curelet.login}</h6>
                                                    </div>
                                                    <div className="col Grid_Table1">
                                                        <h6>  {curelet.login}</h6>
                                                    </div>
                                                    <div className="col">
                                                        <h6>  {curelet.login}</h6>
                                                    </div>
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>);
}
export default Home;